(function() {
  'use strict';

  angular
    .module('on4uTestClient')
    .controller('EditController', EditController);

  /** @ngInject */
  function EditController(toastr, productService, $log, $stateParams, tools) {


    var vm = this;

    vm.productModified = {
      name: "",
      sku: "",
      price: ""
    };

    loadProduct();

    /**
     * Function to load all products in the DB
     */
    function loadProduct() {
      var productId = parseInt($stateParams.productId);
      productService.getProduct(productId).then(function(product){
          vm.productModified = product;
          vm.productBefore = angular.copy(product);
      }).catch(function(error){
        $log.error(error);
      });
    }

    /**
     * Function to modify a product already existing
     */
    vm.edit = function() {
      var nameIsValid = vm.productModified && vm.productModified.name;
      var skuIsValid = vm.productModified && vm.productModified.sku;
      var price = vm.productModified && vm.productModified.price;

      var priceIsValid = price && price > 0;
      if(!priceIsValid){
        toastr.error('El precio del producto debe estar relleno y ser superior a 0.');
      }
      if(!skuIsValid){
        toastr.error('El SKU del producto debe estar relleno.');
      }
      if(!nameIsValid){
        toastr.error('El nombre del producto debe estar relleno.');
      }
      if(priceIsValid && skuIsValid && nameIsValid){
        var index = parseInt(vm.productModified.id);
        productService.updateProduct(index, vm.productModified).then(function(response){
          $log.info("Edit of product correct", response);
          tools.goToProducts();
          toastr.success('Modificación del producto (SKU: ' + vm.productModified.sku +  ') con éxito.');
        }).catch(function(error){
          $log.error(error);
          toastr.success('Error durante la modificación del producto (SKU: ' + vm.product.sku +  ').');
        });
      }

    };

    /**
     * Function to reset all inputs fields about the product
     */
    vm.reset = function() {
      vm.product = vm.productBefore;
    };

    /**
     * Function to return to home
     */
    vm.returnToHome = function() {
      tools.goToProducts();
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('on4uTestClient')
    .controller('CreateController', CreateController);

  /** @ngInject */
  function CreateController($state, productService, $log, tools, toastr) {


    var vm = this;

    vm.product = {
      name: "",
      sku: "",
      price: ""
    };

    /**
     * Function to create a new product from inputs written and verifing them before the creation
     */
    vm.create = function() {
      var nameIsValid = vm.product && vm.product.name;
      var skuIsValid = vm.product && vm.product.sku;
      var price = vm.product && vm.product.price;

      var priceIsValid = price && price > 0;
      if(!priceIsValid){
        toastr.error('El precio del producto debe estar relleno y ser superior a 0.');
      }
      if(!skuIsValid){
        toastr.error('El SKU del producto debe estar relleno.');
      }
      if(!nameIsValid){
        toastr.error('El nombre del producto debe estar relleno.');
      }
      if(priceIsValid && skuIsValid && nameIsValid){
        productService.createProduct(vm.product).then(function(response){
          $log.info("Creation of product correct", response);
          tools.goToProducts();
          toastr.success('Creación del nuevo producto (SKU: ' + vm.product.sku +  ') con exito.');
        }).catch(function(error){
          $log.error(error);
          toastr.error('Error en la creación del nuevo producto (SKU: ' + vm.product.sku +  ').');
        });
      }

    };

    /**
     * Function to clear all inputs in create page
     */
    vm.removeAll = function() {
      vm.product = {
        name: "",
        sku: "",
        price: ""
      };
    };

    /**
     * Function to return to the products page.
     */
    vm.returnToHome = function() {
      tools.goToProducts();
    };

  }
})();

(function() {
  'use strict';

  angular
    .module('on4uTestClient')
    .controller('ConfigController', ConfigController);

  /** @ngInject */
  function ConfigController(productService, toastr, tools) {


    var vm = this;

    vm.newUrlApi = productService.getCurrentUrlApi();

    /**
     * Function to save the configuration written in the input
     */
    vm.save = function() {
      if(vm.newUrlApi){
        var result = productService.modifyUrlApi(vm.newUrlApi);
        if(result){
          vm.returnToHome();
          toastr.success('Configuración guardada con éxito.');
        }
      }else{
        toastr.error('Por favor introduce la URL.');
      }

    };

    /**
     * Function to reset the configuration by default
     */
    vm.reset = function() {
      productService.resetUrlApi();
      vm.newUrlApi = productService.getCurrentUrlApi();
      toastr.success('Configuración restaurada con éxito.');
    };

    /**
     * Function to return to products page
     */
    vm.returnToHome = function() {
      tools.goToProducts();
    };

  }
})();

